import { Table, TableHead, TableHeader, TableBody, TableRow, TableCell } from './table';
import React, { useState, useEffect, useContext } from 'react';
import { Container, PrimaryTitle } from './styled-components';
import { ConnectionsContext } from './contexts';
import { useParams } from 'react-router-dom';
import { createUseStyles } from 'react-jss';
import { mdiDatabase } from '@mdi/js';
import classnames from 'classnames';
import { Http } from '../classes';
import Icon from '@mdi/react';

export default function Database() {    
    const { connections } = useContext(ConnectionsContext);
    const { name } = useParams();

    console.log(connections);

    const activeTableParsed = connections.find(connection => connection.database === name);
    console.log(activeTableParsed);

    const [loadingTable, setLoadingTable] = useState(true);
    const [activeTable, setActiveTable] = useState();
    const [tables, setTables] = useState([]);

    useEffect(() => {
        async function getDatabase() {
            setLoadingTable(true);
            const { code, data } = await Http.get('/databases/tph');
            setLoadingTable(false);
            setTables(data);
        }
        getDatabase();
    }, []);

    console.log(tables);
    
    const getTableData = () => tables.find(data => data.table === activeTable) ?? { columns: {}, rows: [] };

    function renderTable() {
        if (!tables.length || !activeTable) {
            return null;
        }

        const table = getTableData();

        let columns = [];
        for (const property in table.columns) {
            columns.push(property);
        }

        const rows = table.rows.map(row => {
            let cells = [];
            for (const property in row) {
                cells.push(row[property]);
            }
            return (
                <TableRow key={Math.random()}>
                    {
                        cells.map(cell => (
                            <TableCell key={cell}>
                                {cell}
                            </TableCell>
                        ))
                    }
                </TableRow>
            );
        });

        return (
            <>
                <TableHead>
                    {
                        columns.map(column => (
                            <TableHeader key={column}>
                                {column}
                            </TableHeader>
                        ))
                    }
                </TableHead>
                <TableBody>
                    {rows}
                </TableBody>
            </>
        );
    }

    return (
        <Container>
            <PrimaryTitle>
                <Icon path={mdiDatabase} />
                <span>{name}</span>
            </PrimaryTitle>
            <Table>
                {renderTable()}
            </Table>
        </Container>
    );
}
